@import "../../assets/styles/_variables";

.progress {
position: absolute;
  top: 20%;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 4px;
  display: block;
  width: 60%;
  background-color: $color-white;
  border-radius: 2px;
  overflow: hidden;
  }
  .indeterminate {
    background-color: $color-blue-light;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  
    animation: indeterminate 1.5s infinite;
  }
  
  @keyframes indeterminate {
    0% {
      left: -100%;
      width: 100%;
    }
    50% {
      width: 50%;
      left: 25%;
    }
    100% {
      left: 100%;
      width: 0%;
    }
  }
  